#checking-account {
	padding-top: 20px;
	margin-bottom: 20px; }

.checking-account__list {
	@extend .flex-wrp;
	margin: 0 -12px 28px; }

.checking-account__col {
	width: 100%;
	padding: 0 12px;
	margin-bottom: 12px;
	flex-grow: 1; }

.checking-account__item {
	padding: 20px 16px;
	background-color: $bg-gray;
	border-radius: 20px;
	height: 100%;
	img {
		max-width: 32px;
		margin-bottom: 14px; } }

.checking-account__text {
	font-size: 16px; }

@media only screen and (min-width: 640px) {
	.checking-account__col {
		width: 33.33333333%; } }

@media only screen and (min-width: 992px) {
	.checking-account__item {
		display: flex;
		align-items: center;
		img {
			margin-right: 20px;
			flex-shrink: 0; } } }

@media only screen and (min-width: 1520px) {
	#checking-account {
		margin-bottom: 100px; } }
