#package-services {
	padding-top: 20px;
	margin-bottom: 20px; }

.slider-button {
	background-color: rgba(17, 17, 17, .4);
	-webkit-backdrop-filter: blur(8px);
	backdrop-filter: blur(8px); }

.slider-button {
	height: 72px;
	width: 72px;
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto;
	z-index: 2;
	border-radius: 50%;
	background-color: rgba(0, 0, 0, .2);
	cursor: pointer !important;
	opacity: 1;
	transition: opacity .4s ease; }

.package-slider {
	overflow: visible;
	margin-bottom: 30px;
	.slider-button {
		opacity: 0; }
	&:hover {
		.slider-button {
			opacity: 1; } } }

.slider-button {
	.arrow {
		@include posCenter;
		height: 28px;
		width: 16px;
		color: #fff;
		transition: color .2s linear;
		.icon {
			display: block;
			width: 100%;
			height: 100%;
			fill: #fff;
			transition: fill .3s ease; } }
	&:hover {
		.arrow {
			.icon {
				fill: #ea2119; } } } }

.slider-button--prev {
	left: -36px;
	.arrow {
		right: 2px; } }

.slider-button--next {
	right: -36px;
	.arrow {
		left: 2px; } }

.slider-button.swiper-button-disabled {
	display: none; }

.package-slider__inner {
	position: absolute;
	z-index: 1;
	left: 0;
	bottom: 0;
	display: flex;
	flex-direction: column;
	height: 400px;
	width: 100%;
	padding: 45px 60px 48px 60px;
	border-radius: 16px;
	background-color: #f03226;
	transform: translateY(100%);
	transition: transform .5s ease-in-out;
	color: #fff;
	.btn {
		position: relative;
		margin: auto auto 0;
		font-weight: 700;
		border-radius: 30px;
		padding: 19.5px 40px 16.5px;
		z-index: 2; } }

.package-slider__offer {
	margin: 0 0 20px 0;
	.text {
		margin: 0;
		font-size: 24px;
		font-weight: 700;
		line-height: 36px; }
	.item-caption {
		margin: 0;
		font-size: 16px;
		line-height: 22px; } }

.package-slider__item {
	height: 540px;
	max-width: 424px;
	display: flex;
	flex-direction: column;
	padding: 32px 32px 10px 32px;
	border-radius: 16px;
	background-color: #f5f5f7;
	position: relative;
	background-size: contain;
	background-position: bottom center;
	background-repeat: no-repeat;
	overflow: hidden; }

.package-slider__img {
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 0;
	-o-object-fit: contain;
	object-fit: contain;
	-o-object-position: bottom center;
	object-position: bottom center;
	img {
		display: block;
		width: 100%;
		height: 100%;
		-o-object-fit: inherit;
		object-fit: inherit;
		-o-object-position: inherit;
		object-position: inherit;
		border-radius: inherit; } }

.package-slider__ttl {
	font-size: 32px;
	font-weight: 700;
	line-height: 40px;
	margin-top: 13px;
	color: #f03226;
	text-align: center;
	position: relative; }

.package-slider__mobile-switch {
	height: 28px;
	width: 64px;
	border-radius: 16px;
	background-color: rgba(255, 255, 255, .9);
	position: absolute;
	z-index: 2;
	top: 90%;
	left: 50%;
	transform: translateX(-50%);
	transition: top .5s ease-in-out;
	-webkit-animation: wiggle-switch 1.25s ease-in-out infinite;
	animation: wiggle-switch 1.25s ease-in-out infinite;
	> .arrow {
		height: 8px;
		width: 28px;
		position: absolute;
		top: 50%;
		left: 50%;
		color: #fff;
		transform: translate(-50%, -50%);
		transform-origin: center center; }
	.icon {
		display: block;
		width: 100%;
		height: 100%;
		fill: #fff; } }

.show-inner {
	.package-slider__mobile-switch {
		.icon {
			transform: rotate(180deg); } } }

.package-slider__mobile-switch {
	-webkit-backdrop-filter: saturate(180%) blur(8px);
	backdrop-filter: saturate(180%) blur(8px);
	background-color: rgba(255, 255, 255, .1); }

@keyframes wiggle-switch {
	0% {
		transform: translate(-50%, 0); }
	50% {
		transform: translate(-50%, -12px); }
	100% {
		transform: translate(-50%, 0); } }

.package-slider__item.show-inner > .package-slider__mobile-switch {
	top: 2%;
	-webkit-animation: none;
	animation: none; }

.package-slider__item.show-inner > .package-slider__mobile-switch {
	background-color: rgba(255, 255, 255, .2); }

.package-slider__item.show-inner > .package-slider__inner {
	transform: translateY(0); }

.package-slider__clicked-overlay {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0; }

@media only screen and (max-width: 599px) {
	.package-slider__item {
		height: 380px;
		max-width: 284px;
		padding-top: 26px;
		transform: scale(.9);
		transition: transform .4s ease-in-out; }
	.package-slider__item.swiper-slide-active, .package-slider__item.swiper-slide-duplicate-active {
		transform: scale(1); }
	.package-slider__ttl {
		margin-top: 16px;
		margin-bottom: 16px;
		font-size: 24px;
		line-height: 32px; }
	.package-slider__inner {
		height: 100%;
		padding: 32px 24px 24px 24px;
		.btn {
			padding: 15.5px 40px 12.5px; } }
	.slider-button {
		display: none; } }

@media only screen and (min-width: 1000px) {
	.package-slider__mobile-switch {
		display: none; }
	.package-slider__item {
		&:hover {
			> .package-slider__inner {
				transform: translateY(0);
				box-shadow: 0 -4px 44px 0 rgba(240, 50, 38, .5); } } } }

@media only screen and (min-width: 1520px) {
	.package-slider {
		margin-bottom: 100px; }
	#package-services {
		margin-bottom: 100px; } }
