#internet-bank {
	padding-top: 20px;
	margin-bottom: 20px; }

.internet-bank__img {
	margin-bottom: 24px;
	text-align: center;

	img {
		border-radius: 20px; } }

.internet-bank__text {
	h3, .h3 {
		margin-bottom: 20px;
		font-weight: 700;
		font-size: 24px;
		line-height: 1.33; } }

.internet-bank-list__icon {
	position: relative;
	display: inline-block;
	width: 52px;
	height: 52px;
	margin-bottom: 12px;
	background: url("../images/icon_bg.svg") center / cover no-repeat;

	img {
		@include posCenter;
		max-width: 32px;
		max-height: 32px; } }

.internet-bank-list {
	@extend .flex-wrp;
	margin: 0 -12px; }

.internet-bank-list__col {
	width: 100%;
	padding: 0 12px;
	margin-bottom: 20px;
	font-size: 16px; }

.apps {
	@extend .flex-wrp;
	margin: 0 -6px; }

.apps__item {
	padding: 0 6px; }

.apps__link {
	display: inline-block;
	padding: 12px 18px 8px;
	background-color: $bg-gray;
	border-radius: 30px;
	text-decoration: none;
	font-size: 16px;
	font-weight: 600;
	span {
		display: block;
		position: relative;
		padding-left: 30px; }
	.icon {
		position: absolute;
		left: 0;
		top: -6px;
		bottom: 0;
		margin: auto;
		fill: $red;
		width: 20px;
		height: 20px; } }

@media only screen and (min-width: 640px) {
	.internet-bank-list__col {
		width: 50%; } }

@media only screen and (min-width: 768px) {
	.internet-bank-info {
		display: flex;
		flex-wrap: wrap; }
	.internet-bank__img {
		margin-bottom: 0;
		width: 46%; }
	.internet-bank__text {
		padding-left: 15px;
		width: 54%; } }

@media only screen and (min-width: 1200px) {

	.internet-bank-list__col {
		padding: 0 20px;
		margin-bottom: 40px;
		font-size: 18px;
		line-height: 1.44;

		p {
			max-width: 240px; } }
	.internet-bank-list__icon {
		margin-bottom: 16px;
		width: 64px;
		height: 64px; }
	.internet-bank__text {
		padding-left: 60px;

		.h3, h3 {
			margin-bottom: 40px;
			font-size: 40px;
			line-height: 1.2; } }
	.apps {
		margin: 0 -12px; }
	.apps__item {
		padding: 0 12px; }
	.apps__link {
		padding: 15px 32px 10px;
		font-size: 18px;
		transition: all .3s ease;
		.icon {
			transition: fill .3s ease; }
		&:hover {
			color: #fff;
			background: #EA2119;
			box-shadow: 0 2px 12px rgba(240, 50, 38, 0.4);
			.icon {
				fill: #fff; } } } }

@media only screen and (min-width: 1320px) {
	.internet-bank__text {
		padding-left: 60px;
		padding-left: 120px; } }

@media only screen and (min-width: 1520px) {
	#internet-bank {
		margin-bottom: 100px; } }
