#advantages {
	padding-top: 20px;
	margin-bottom: 20px; }
.advantages-list {
	margin: 0 -12px; }

.advantages-list__col {
	width: 100%;
	padding: 0 12px;
	margin-bottom: 12px; }

.advantages-list__item {
	border-radius: 16px;
	padding: 16px;
	background-color: $bg-gray; }

.advantages-list__info {
	h3,.h3 {
		margin-bottom: 4px;
		font-size: 20px; }
	span {
		color: $red; } }

.advantages-list_img {
	display: none; }

@media only screen and (min-width: 768px) {
	.advantages-list {
		display: flex;
		flex-wrap: wrap; }
	.advantages-list__col {
		width: 50%;
		margin-bottom: 24px; }
	.advantages-list__item {
		display: flex;
		flex-wrap: wrap;
		height: 100%; }
	.advantages-list__info {
		width: 70%;
		padding-right: 15px; }
	.advantages-list_img {
		display: block;
		width: 30%; } }

@media only screen and (min-width: 1200px) {
	.advantages-list__item {
		padding: 32px 12px 32px 40px; }
	.advantages-list__info {
		width: 55%;
		padding-right: 24px;
		h3,.h3 {
			margin-bottom: 8px;
			font-size: 32px;
			line-height: 1.25; }
		p {
			font-size: 18px;
			line-height: 1.44; } }
	.advantages-list_img {
		width: 45%; } }
@media only screen and (min-width: 1520px) {
	#advantages {
		margin-bottom: 100px; } }
