.tabs {
	text-align: center; }

.tab-links-wrp {
	display: inline-block;
	margin-bottom: 20px; }

.tab-links {
	display: flex;
	flex-wrap: wrap;
	margin: 0 auto;
	padding: 2px;
	border-radius: 50px;
	background-color: $bg-gray; }

.js-tabs-lnk {
	display: inline-block;
	padding: 8px 16px 3px;
	border-radius: 50px;
	font-weight: 600;
	text-align: center;
	text-decoration: none;
	color: $base; }

.js-tabs-lnk_active {
	background-color: $red;
	color: #fff; }

.js-tab {
	display: none;
	text-align: left;
	&:nth-child(2n) {
		.internet-bank__img {
			order: 1; }
		.internet-bank__text {
			padding-left: 0;
			padding-right: 15px; } } }

.js-tab_show {
	display: block; }

@media only screen and (min-width: 1200px) {
	.tab-links-wrp {
		margin-bottom: 64px; }
	.js-tabs-lnk {
		padding: 11px 18px 5px;
		font-size: 16px;
		transition: all .3s ease;
		&:hover {
			color: $red; } }
	.js-tabs-lnk_active {
		&:hover {
			color: #fff; } }
	.js-tab {
		&:nth-child(2n) {
			.internet-bank__text {
				padding-right: 60px; } } } }

@media only screen and (min-width: 1320px) {
	.js-tab {
		&:nth-child(2n) {
			.internet-bank__text {
				padding-right: 120px; } } } }
