$font: Glober;
$red: #F03226;
$green: #0AB205;
$base: #111;
$gray: #959595;
$bg-gray: #F5F5F7;

@mixin posCenter() {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto; }

@mixin posLeftCenter() {
	position: absolute;
	left: 0;
	bottom: 0;
	top: 0;
	margin: auto; }

@mixin posRightCenter() {
	position: absolute;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto; }

@mixin placeholder($color) {
	&::-webkit-input-placeholder {
		color: $color; }
	&:-moz-placeholder {
		color: $color; }
	&::-moz-placeholder {
		color: $color; }
	&:-ms-input-placeholder {
		color: $color; } }


@mixin font($font_name, $file__name, $weight, $style) {
	@font-face {
		font-family: $font_name;
		font-display: swap;
		src: url('../fonts/' + $file__name + '.woff') format('woff'), url('../fonts/' + $file__name + '.woff2') format('woff2');
		font-weight: $weight;
		font-style: $style; } }

