.main {
	min-height: 100%;
	overflow: hidden; }

.main__container {
	max-width: 1344px;
	margin: 0 auto;
	padding: 0 12px; }

.main__row {
	margin: 0 -12px; }

.flex-wrp {
	display: flex;
	flex-wrap: wrap; }

.hide {
	display: none !important; }

.show {
	display: block !important; }

h2,.h2 {
	margin-bottom: 16px;
	font-size: 28px;
	line-height: 1.3; }
.object-fit {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	object-fit: cover; }
@media only screen and (min-width: 768px) {
	h2,.h2 {
		text-align: center; } }

@media only screen and (min-width: 1200px) {
	h2,.h2 {
		margin-bottom: 30px;
		font-size: 48px;
		line-height: 1.17; } }
