.header__container {
	max-width: 1368px;
	margin: 0 auto;
	padding: 0 24px;
	@extend .flex-wrp;
	justify-content: space-between;
	align-items: center; }

.header__logo {
	display: inline-block;
	width: 108px; }

.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	margin: auto;
	padding: 15px 0;
	z-index: 12; }

.header_fixed {
	position: fixed;
	background-color: $bg-gray; }

.header_fixed-top {
	box-shadow: 0 0 4px rgba(0, 0, 0, 0.1); }

@media only screen and (min-width: 768px) {
	.header__logo {
		width: 161px; } }
