.popup {
	position: relative;
	width: 100%;
	max-width: 1096px;
	background: none;
	padding: 0 8px;
	.my-form {
		position: relative;
		padding-top: 40px;
		padding-bottom: 20px; } }

.popup_hide {
	display: none; }

.popup__inner {
	position: relative;
	padding: 18px;
	background-color: #fff; }

.popup__ttl {
	position: relative;
	margin-bottom: 26px;
	font-size: 24px; }

.close-popup {
	position: absolute;
	top: 15px;
	right: 15px;
	width: 18px;
	height: 18px;
	padding: 0;
	border: none;
	background-color: transparent;
	cursor: pointer;
	z-index: 1;
	.icon {
		@include posCenter();
		width: 13px;
		height: 13px;
		fill: $gray;
		transition: fill .3s ease; }
	&:hover {
		.icon {
			fill: $red; } } }

@media only screen and (min-width: 1200px) {
	.popup {
		.my-form {
			position: relative;
			padding-top: 65px;
			padding-bottom: 34px; } } }
