.catalog-elements {
	@extend .flex-wrp;
	margin: 0 -12px 20px; }

.catalog-elements-wrapper {
	width: 100%;
	max-width: 1096px;
	margin: 0 auto 20px;
	h2,.h2 {
		text-align: left; } }

.catalog-elements__item {
	width: 100%;
	margin-bottom: 20px;
	padding: 0 12px;
	p {
		font-size: 16px; }
	.h3,h3 {
		margin-bottom: 4px;
		font-size: 20px;
		font-weight: 700;
		line-height: 1.4; } }

.catalog-elements__icon {
	position: relative;
	width: 52px;
	height: 52px;
	margin-bottom: 12px;
	background: url('../images/icon_bg-red.svg') center / cover no-repeat;

	img {
		@include posCenter;
		max-width: 52px;
		max-height: 52px; } }

@media only screen and (min-width: 640px) {
	.catalog-elements__item {
		width: 50%;
		p {
			max-width: 312px; } } }

@media only screen and (min-width: 768px) {
	.catalog-elements__item {
		width: 33.33333333%;
		p {
			font-size: 18px;
			line-height: 1.44; }
		.h3,h3 {
			font-size: 24px;
			line-height: 1.33; } } }
@media only screen and (min-width: 1200px) {
	.catalog-elements__icon {
		margin-bottom: 24px;
		width: 64px;
		height: 64px; } }
@media only screen and (min-width: 1520px) {
	.catalog-elements-wrapper {
		margin-bottom: 100px; } }
