.footer {
	padding: 20px 0;
	background-color: #222;
	font-size: 12px;
	line-height: 1.25;
	color: #fff;
	text-align: center; }

.copyright {
	margin-bottom: 10px;
	span {
		display: block; }
	img {
		max-width: 20px;
		margin-bottom: 20px; } }

.js-up {
	position: fixed;
	right: -50px;
	bottom: 58px;
	cursor: pointer;
	width: 30px;
	height: 30px;
	box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.2);
	border-radius: 50%;
	transition: right .3s ease;
	background-color: #fff;
	z-index: 5;
	.icon {
		width: 12px;
		height: 7px;
		@include posCenter;
		fill: $red; } }

.js-up_show {
	right: 25px; }
@media only screen and (min-width: 640px) {
	.copyright {
		display: flex;
		align-items: center;
		justify-content: center;
		img {
			margin-right: 16px;
			margin-bottom: 0; } } }
@media only screen and (min-width: 1200px) {
	.js-up {
		width: 48px;
		height: 48px;
		.icon {
			width: 20px;
			height: 12px; } } }

@media only screen and (min-width: 1600px) {
	.js-up_show {
		right: 50px; } }
