.base-advan {
	margin-top: -32px;
	margin-bottom: 20px; }

.base-advan-list {
	position: relative;
	z-index: 1;
	margin-bottom: 40px;
	padding: 16px 0;
	background: #FFFFFF;
	box-shadow: 0 3px 20px rgba(0, 0, 0, 0.1);
	border-radius: 16px; }

.base-advan-list__col {
	padding: 0 20px;
	margin-bottom: 24px;
	&:last-child {
		margin-bottom: 0; } }

.base-advan-list__ttl {
	font-size: 18px;
	font-weight: 700;
	line-height: 1.44; }

@media only screen and (min-width: 640px) {
	.base-advan-list {
		display: flex;
		flex-wrap: wrap;
		padding: 24px 0 0; }
	.base-advan-list__col {
		width: 50%; } }

@media only screen and (min-width: 768px) {
	.base-advan {
		margin-top: -60px; } }

@media only screen and (min-width: 1200px) {
	.base-advan-list {
		padding: 33px 24px 0;
		margin-bottom: 33px; }
	.base-advan-list__col {
		width: 25%;
		padding: 0 16px;
		margin-bottom: 33px; }
	.base-advan-list__ttl {
		font-size: 20px; } }

@media only screen and (min-width: 1360px) {
	.base-advan-list__ttl {
		font-size: 24px;
		line-height: 1.33;
		margin-bottom: 2px; }
	.base-advan-list {
		p {
			font-size: 16px; } } }
@media only screen and (min-width: 1520px) {
	.base-advan {
		margin-bottom: 100px; } }
