.my-form {
	max-width: 1096px;
	margin: 0 auto;
	padding: 23px 16px 0;
	background: #FFF;
	box-shadow: 0 3px 20px rgba(0, 0, 0, 0.1);
	border-radius: 16px;

	.h3, h3 {
		margin-bottom: 16px;
		font-size: 24px;
		font-weight: 700;
		line-height: 1.33;
		text-align: center; } }

.my-form__body {
	margin: 0 auto;
	max-width: 674px; }

.confidentiality {
	font-size: 12px;
	color: $gray;

	a {
		text-decoration: none;

		&:hover {
			text-decoration: underline; } } }

.btn {
	display: inline-block;
	padding: 11.5px 24px 7.5px;
	text-decoration: none;
	text-align: center;
	color: #fff;
	background-color: $red;
	border-radius: 24px;
	border: none;
	font-size: 16px;
	&[disabled] {
		cursor: default;
		background-color: #F7A6A3; } }
.btn_white {
	background-color: #fff;
	color: $red; }
.fieldset {
	position: relative; }

.legend {
	position: absolute;
	bottom: 100%;
	left: 12px;
	z-index: 1;
	padding: 0 4px;
	font-size: 12px;
	color: $gray;
	margin-bottom: -9px;
	background-color: #fff;

	sup {
		color: $red; } }

.my-field-wrapper {
	margin-bottom: 15px; }

.my-form__col {
	width: 100%;
	padding: 0 12px;
	margin-bottom: 20px;
	flex-grow: 1; }

.my-form__note {
	width: 100%;
	padding: 0 12px;
	margin-bottom: 24px;
	text-align: center;
	color: $gray; }

.my-form__col_btn {
	padding: 0 12px;
	margin-bottom: 20px;
	text-align: center; }

.my-form__row {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	margin: 0 -12px; }

.my-field {
	height: 40px;
	width: 100%;
	padding: 0 12px 0 14px;
	border: 1px solid rgba(149, 149, 149, 0.4);
	border-radius: 8px;
	@include placeholder(#ccc);
	transition: border-color .3s ease;

	&:focus {
		border-color: $gray; } }

.switch-wrapper {
	text-align: center; }

.switch-inner {
	display: inline-block;
	margin-bottom: 24px; }

.switch {
	@extend .tab-links; }

.switch__item {
	position: relative; }


.switch-radio-label {
	@extend .js-tabs-lnk;
	cursor: pointer; }

.switch-radio {
	@include posCenter;
	visibility: hidden;
	opacity: 0;

	&:checked + .switch-radio-label {
		background-color: $red;
		color: #fff;
		transition: all .3s ease; }

	&:not(:checked) + .switch-radio-label:hover {
		color: $red; } }

.g-recaptcha {
	overflow: hidden;
	max-width: 100%; }

.g-recaptcha iframe, .g-recaptcha > div, .g-recaptcha body > div[style]:last-of-type {
	transform: scale(0.9);
	transform-origin: left top; }

.btn-center {
	text-align: center; }

.form-message {
	text-align: center;
	font-weight: 600;
	font-size: 16px; }

.err_mess {
	margin-bottom: 12px;
	color: $red; }

.complete {
	margin-bottom: 12px;
	color: $green; }

.err {
	border-color: $red; }

@media only screen and (max-width: 1199px) {
	.gray-form {
		.my-form {
			padding: 0;
			box-shadow: none; }

		.my-form__fields {
			padding: 23px 16px 0;
			background: #FFF;
			box-shadow: 0 3px 20px rgba(0, 0, 0, 0.1);
			border-radius: 16px; } } }

@media only screen and (max-width: 639px) {
	.my-form__col_btn {
		order: 1; } }

@media only screen and (min-width: 640px) {
	.my-form__col {
		width: 50%; } }

@media only screen and (min-width: 768px) {
	.err_mess, .complete, .my-form__col {
		margin-bottom: 24px; }
	.my-form__col_btn {
		margin-bottom: 24px; }
	.my-form {
		padding: 51px 16px 24px; } }

@media only screen and (min-width: 1200px) {
	.my-form__note {
		margin-bottom: 30px;
		text-align: left;
		font-size: 18px;
		line-height: 1.44; }
	.my-form {
		.h3, h3 {
			font-size: 32px;
			line-height: 1.25;
			text-align: left; } }
	.gray-form {
		padding: 30px 0;
		background-color: $bg-gray; }
	.btn {
		&:not([disabled]) {
			transition: all .3s ease;
			&:hover {
				background-color: #EA2119;
				box-shadow: 0 5px 15px rgba(240, 50, 38, 0.4);
				&.btn_white {
					background-color: rgba(255,255,255,.8); } }
			&:active {
				background-color: #C0281E;
				box-shadow: none;
				transition: none;
				span {
					opacity: .6; } } } }
	.switch-inner {
		margin-bottom: 34px; } }

@media only screen and (min-width: 1520px) {
	.gray-form {
		padding: 120px 0; } }
