.menu__link {
	font-size: 18px;
	color: $base;
	text-decoration: none; }

.menu__link_active {
	color: $red; }

.nav__close {
	display: none; }
@media only screen and (max-width: 767px) {
	.nav__close {
		display: block;
		position: absolute;
		right: 12px;
		top: 9px;
		width: 30px;
		height: 30px;
		.icon {
			@include posCenter;
			width: 16px;
			height: 16px;
			fill: $base; } }
	.js-toggle-menu {
		position: relative;
		display: flex;
		width: 30px;
		height: 30px;
		align-items: center;
		z-index: 10;
		transition: color .3s ease; }
	.toggle-menu {
		display: block;
		@include posCenter;
		width: 16px;
		height: 10px;
		cursor: pointer;
		.toggle-menu_center, &:before, &:after {
			display: block;
			position: absolute;
			width: 16px;
			height: 2px;
			left: 0;
			background-color: $base;
			border: none;
			border-radius: 4px;
			transition: transform 0.5s, background-color 0.2s; }
		&:before, &:after {
			content: ''; }
		&:before {
			top: 0; }
		&:after {
			width: 12px;
			bottom: 0; }
		.toggle-menu_center {
			top: 0;
			bottom: 0;
			margin: auto; } }
	.nav-shadow {
		display: none;
		position: fixed;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 10; }
	.nav-shadow_active {
		display: block; }
	.nav {
		display: flex;
		flex-direction: column;
		position: fixed;
		padding: 65px 16px 16px;
		width: 100%;
		max-width: 450px;
		height: 100%;
		right: -100%;
		top: 0;
		overflow-y: auto;
		transition: right .4s ease;
		z-index: 11;
		background-color: #fff;
		.btn {
			width: 100%;
			background-color: $base;
			border-color: $base;
			color: #fff; } }
	.nav_open {
		right: 0; }
	.menu {
		margin-bottom: 15px;
		position: relative;
		overflow-y: auto;
		z-index: 1;
		text-align: center; }
	.menu__item {
		margin-bottom: 10px; } }

@media only screen and (min-width: 768px) {
	.menu {
		display: flex;
		align-items: center;
		justify-content: center; }
	.menu__item {
		position: relative;
		display: flex;
		justify-content: center;
		flex-basis: auto;
		flex-grow: 1;
		padding: 0 7px;
		&:last-child {
			padding-right: 0; } }
	.menu__link {
		font-size: 12px; } }

@media only screen and (min-width: 992px) {
	.menu__item {
		padding: 0 12px; }
	.menu__link {
		font-size: 16px; } }

@media only screen and (min-width: 1200px) {
	.menu__item {
		padding: 0 17px; }
	.menu__link {
		font-size: 18px;
		transition: color .3s ease;
		&:hover {
			color: $red; } } }
