.main__top {
	max-width: 1520px;
	margin: 0 auto; }

h1, .h1 {
	margin-bottom: 8px;
	font-size: 32px;
	line-height: 1.25; }

.top__body {
	position: relative;
	padding-top: 116px;
	background: url("../images/top-bg.jpg") top center / cover no-repeat;
	border-radius: 0 0 16px 16px;
	overflow: hidden; }

.top__container {
	width: 100%;
	max-width: 1368px;
	margin: 0 auto;
	display: flex;
	flex-wrap: wrap; }

.top__text {
	padding: 0 24px;
	margin-bottom: 50px; }
.top__text-body {
	p {
		font-size: 16px; } }

@media only screen and (min-width: 640px) {
	.top__container {
		padding: 0 24px; }
	.top__body {
		padding-top: 0; }
	.top__img {
		position: absolute;
		right: 0;
		bottom: 0;
		display: flex;
		align-items: flex-end;
		width: 50%; }
	.top__text {
		width: 50%;
		padding: 80px 0;
		margin-bottom: 0;
		align-self: center; } }
@media only screen and (min-width: 768px) {
	.top__container {
		min-height: 400px; }
	.top__text {
		padding: 100px 0; } }
@media only screen and (min-width: 1200px) {
	.top__container {
		min-height: 600px; }
	.top__text-body {
		max-width: 536px;
		p {
			font-size: 24px; } }
	.h1, h1 {
		margin-bottom: 16px;
		font-size: 56px;
		line-height: 1.17; } }

@media only screen and (min-width: 1520px) {
	.top__container {
		min-height: 688px; }
	.top__img {
		width: 56.6%; } }
