#coworking {
	margin-bottom: 0; }
.base-section {
	padding-top: 20px;
	margin-bottom: 20px;
	/*h2, .h2
		text-align: left */ }

.base-section__img {
	position: relative;
	margin-bottom: 24px;
	height: 250px;
	img {
		border-radius: 20px; } }

.base-section__gray {
	padding: 32px 24px;
	margin-bottom: 40px;
	border-radius: 16px;
	background-color: $bg-gray;
	text-align: center; }

.base-section__gray-text {
	margin-bottom: 16px;
	font-size: 24px;
	font-weight: 700;
	line-height: 1.33; }

@media only screen and (min-width: 640px) {
	.base-section__gray {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 32px 24px 28px;
		.btn {
			white-space: nowrap; } }
	.base-section__gray-text {
		padding-right: 15px;
		margin-bottom: 0;
		text-align: left; } }
@media only screen and (min-width: 1200px) {
	.base-section__gray {
		padding: 42px 48px 38px; }
	.base-section__gray-text {
		max-width: 760px;
		font-size: 32px;
		line-height: 1.25; }
	.base-section__img {
		margin-bottom: 64px;
		height: 420px; } }

@media only screen and (min-width: 1520px) {
	.base-section {
		margin-bottom: 100px; } }
